@import '~antd/dist/antd.css';

.app {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.table {
  padding: 10px;
  margin: 5px 0 0 0;
  width: 100%;
  border: 1px solid #f0f0f0;
  border-radius: 10px;

}

.table:hover {
  box-shadow: 0 0 5px 4px #f0f0f0;
}

.tableRow {
  font-size: 16px;
  cursor: pointer;
}
.tableRow:hover td {
  background-color: #eafbff !important;
}

.tableRowSelected {
  font-size: 16px;
  cursor: pointer;
}

.tableRowSelected td {
  background-color: #d9fcff !important;
}

.tableActions button:first-child {
  margin: 0 15px 0 0;
}

.header {
  display: flex;
  justify-content: space-between;
}

.currentManager {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 5px;
}

.dropdownManager:hover {
  box-shadow: none !important;
}

.currentManager:hover {
  box-shadow: 0 0 5px 4px #f0f0f0;
}

.currentManager img {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.headerText {
  display: flex;
  justify-content: flex-end;
}

.crmDescription {
  font-family: Bender;
  align-self: flex-end;
  margin: 0 15px 0 0;
  font-size: 35px;
  color: rgba(4, 1, 1, 0.93);
}

.crmName {
  font-family: Bender;
  font-size: 55px;
  font-style: italic;
  color: #ff2947;
  font-weight: bold;
}

.tableDescription {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 30px 0 0 0;
  font-family: Bender;
  font-size: 2vw;
  color: rgba(0, 0, 0, 0.75);
  font-weight: bold;
}

.commentImg {
  width: 30px;
  height: 30px;
}

.commentImg:hover {
  opacity: 0.6;
}

.statRow {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.statRow > div {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.statRow > div:first-child {
  margin: 0 30px 0 0;
}

.admin{
  color: #f05a6c;
}

@media screen and (max-width: 800px) {
  .crmDescription {
    font-size: 20px;
  }
  .crmName {
    font-size: 35px;
  }
}

@media screen and (max-width: 550px) {
  .header {
    flex-direction: column-reverse !important;
  }
}
@media screen and (max-width: 730px) {
  .tableDescription {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media screen and (max-width: 1200px) {
  .statRow {
    flex-direction: column;
    justify-content: center;
  }
  .statRow > div {
    width: 100%;
  }
}
