.workerStatistics {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    padding: 10px 30px;
    border: 1px solid gainsboro;
    box-shadow: 0 0 5px 2px #e3e3e3;
    max-height: 300px;
    overflow-y: auto;
}

.salesFunnelLabel {
    margin: 30px 0 0 0;
    font-family: Bender;
    font-size: 2vw;
    color: rgba(0, 0, 0, 0.75);
    font-weight: bold;
}

.workerRow {
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
    border-bottom: 1px solid gray;
}

.workerRow:last-child {
    border: none;
}

.managerInfoStatistics {
    font-family: Roboto;
    font-size: 16px;
    color: #5896f5;
}

.allClients {
    font-size: 15px;
    color: #656565;
    margin: 0 30px 0 0;
}

.clientCount {
    font-size: 30px;
    color: #53f05a;
}

.callsStatistics {
    font-size: 13px;
    font-weight: bold;
}

.outGoing {
    color: #36f09f;
    font-size: 20px;
}
.missed {
    color: #f05a6c;
    font-size: 20px;
}

