.commentsModal .ant-modal-content>div{
    border: none !important;
}

.commentsModalBody {
    display: flex;
    flex-direction: column;
}

.clientDescriptionInComment {
    display: flex;
    flex-direction: column;
}
.clientDescriptionInComment > div span {
    color: #5896f5;
    font-size: 16px;
}

.activeComment {
    color: #f55182;
    font-size: 16px;
    cursor: pointer;
}

.comments {
    display: flex;
    flex-direction: column;
    margin: 20px 0 0 0;
    padding: 0 5px 0 0;
    height: 250px;
    overflow-y: auto;
}

.comment {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    margin: 10px 0 0 0;
}

.comment:first-child {
    margin: 0 !important;
}
.commentDescription {
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    color: darkgray;
}

.commentTextBox {
    border: 1px solid #e3e3e3;
    padding: 5px 10px;
    font-size: 18px;
    color: rgba(38, 39, 45, 0.61);
    margin: 3px 0 0 0;
    word-wrap: normal;
}

.addCommentBox {
    display: flex;
    flex-direction: column;
    margin: 10px 0 0 0;
}

.addCommentBox button {
    margin: 10px 0 0 0;
    align-self: flex-end;
}
