.salesFunnel {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 30px;
    max-width: 800px;
    border: 1px solid gainsboro;
    box-shadow: 0 0 5px 2px #e3e3e3;
}

.salesFunnelLabel {
    margin: 30px 0 0 0;
    font-family: Bender;
    font-size: 2vw;
    color: rgba(0, 0, 0, 0.75);
    font-weight: bold;
}

.salesFunnelRow {
    display: flex;
    justify-content: center;
    color: black;
    font-weight: bold;
    font-size: 13px;
    margin: 7px 0 0 0;
    padding: 0 5px;
    min-width: 5%;
    white-space: nowrap;
}
